import { useState, useEffect } from "react";
import { useLocation} from 'react-router-dom';
import "./Price.scss"
import PriceAPI from "../../API/PiceAPI";

export default function Price() {
    const location = useLocation();
    const [priceInfo, setPriceInfo] = useState(null)
    const [density, setDensity] = useState(35)

    useEffect(() => {
        new PriceAPI().get(location.search).then((data) => { 
            setPriceInfo(data); 
        });
        const urlParams = new URLSearchParams(location.search);
        setDensity(urlParams.get("density") || 35);   
    }, [location.search]); 
    return (
        <div className="Price-page">
            <div className="Price-page__title">
                <h1></h1>
                <p>     </p>
            </div>
            <div className="Price-page__info">
                <p className="Price-page__info-title">  ,     {density} :</p>
                {
                    priceInfo == null
                    ||
                    <>
                        <ul className="Price-page__list browser-default">
                            {
                                priceInfo.map( (item, key) => 
                                    <li className="Price-page__item" key={key}>
                                        {`${item.width}  - ${item.price} .  `}
                                    </li>
                                )
                            }
                        </ul>
                    </>
                }
            </div>
            <hr/>
            <iframe width="1028" height="578" src="https://www.youtube.com/embed/gAhW8YFKDZg" title="      &quot;   &quot;.  ." frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerPolicy="strict-origin-when-cross-origin" allowFullScreen></iframe>
        </div>
    );
}